$icons: (0:0);
$icons: map-merge($icons,(arrow-left: (X: -106px, Y:0px, W: 81px, H: 23px, TW: 187px, TH: 147px, IMG: '../img/icons.png')));
$icons: map-merge($icons,(arrow-right: (X: -106px, Y:-27px, W: 81px, H: 23px, TW: 187px, TH: 147px, IMG: '../img/icons.png')));
$icons: map-merge($icons,(eco-1: (X: -41px, Y:-98px, W: 35px, H: 49px, TW: 187px, TH: 147px, IMG: '../img/icons.png')));
$icons: map-merge($icons,(eco-2: (X: -106px, Y:-54px, W: 49px, H: 36px, TW: 187px, TH: 147px, IMG: '../img/icons.png')));
$icons: map-merge($icons,(eco-3: (X: 0px, Y:-98px, W: 37px, H: 49px, TW: 187px, TH: 147px, IMG: '../img/icons.png')));
$icons: map-merge($icons,(eco-4: (X: 0px, Y:0px, W: 49px, H: 49px, TW: 187px, TH: 147px, IMG: '../img/icons.png')));
$icons: map-merge($icons,(eco-5: (X: 0px, Y:-53px, W: 49px, H: 41px, TW: 187px, TH: 147px, IMG: '../img/icons.png')));
