/* =================================== Main Styles @mixin ================================== */
body { position: relative; min-width: 320px; font-family: "Circe-Light", Arial, sans-serif; font-size: 19px; color: #323232; line-height: 1.4; }

.wrapper { position: relative; overflow: hidden; margin: 0 auto; min-height: 100%; }

.container { margin: 0 auto; padding: 0 20px; max-width: 1380px; }

/* Helpers ----------------------------------- */
button { background-color: transparent; }

a:hover, a:focus { text-decoration: none; }

a { color: inherit; text-decoration: none; }

table { vertical-align: top; }

img { max-width: 100%; vertical-align: top; }

.icon { display: inline-block; vertical-align: middle; }

.text-left { text-align: left; }

.text-center { text-align: center; }

.text-right { text-align: right; }

.is-hidden { display: none; }

.vertical { display: inline-block; vertical-align: middle; }

.cfx:after { content: ""; display: table; clear: both; }

.btn { display: inline-block; padding: 15px 20px 12px; font-family: "Circe-Bold", Arial, sans-serif; font-size: 15px; color: #ffffff; background-color: #004281; border: 2px solid #004281; border-radius: 3px; text-transform: uppercase; letter-spacing: 0.04em; transition: all 0.25s; }

.btn:hover { background-color: #00284e; border-color: #00284e; }

.btn_light { background-color: #0068cc; border-color: #0068cc; }

.btn_light:hover { background-color: #004e99; border-color: #004e99; }

.btn_white { color: #ffffff; background-color: transparent; border-color: #ffffff; }

.btn_white:hover { color: #004281; background-color: #ffffff; border-color: #ffffff; }

.btn__fa { margin-right: 10px; }

.btn__fa_right { margin-right: 0; margin-left: 10px; }

.title { font-family: "Circe-Bold", Arial, sans-serif; font-size: 34px; color: #004281; text-align: center; }

.title__color { display: block; margin-top: -3px; font-family: "Circe-Light", Arial, sans-serif; font-size: 22px; line-height: 1.5; }

.title_dark { color: #272727; }

/* =================================== Header @mixin ================================== */
.header { position: fixed; z-index: 100; top: 0; left: 0; right: 0; }

.header__top { padding-top: 13px; padding-bottom: 11px; font-size: 15px; color: #ffffff; background-color: #004281; }

.header__top:after { content: ""; display: table; clear: both; }

.header__top-text { float: left; }

.header__socials { float: right; }

.header__bottom { padding-top: 20px; padding-bottom: 20px; background-color: #ffffff; }

.header__bottom:after { content: ""; display: table; clear: both; }

.header__bottom-logo { float: left; padding: 4px 8px 2px; margin-left: 10px; color: #004281; font-size: 14px; border: 1px solid #004281; }

.header__bottom-nav { margin-top: 6px; float: right; }

.nav { font-size: 0; }

.nav:after { content: ""; display: table; clear: both; }

.nav__link { display: inline-block; vertical-align: middle; margin: 0 10px; font-family: "Circe-Regular", Arial, sans-serif; font-size: 14px; color: #004281; transition: all 0.25s; }

.nav__link:hover { color: #0068cc; }

.socials:after { content: ""; display: table; clear: both; }

.socials__item { float: left; margin-left: 20px; font-size: 15px; }

.socials__fa { margin-right: 5px; color: #659fff; }

.socials__fa .fa-phone { position: relative; top: 2px; }

/* =================================== Footer @mixin ================================== */
/* =================================== Main @mixin ================================== */
.main { padding-top: 112px; }

/* Screen ----------------------------------- */
.screen { overflow: hidden; position: relative; padding-top: 140px; padding-bottom: 140px; }

.screen__bg { position: absolute; z-index: 1; top: 0; left: 0; right: 0; height: 100%; background: url(../img/bg/screen.jpg) center top no-repeat; background-size: cover; }

.screen__container { position: relative; z-index: 2; }

.screen .screen__inner { margin-left: 120px; }

.screen__title { font-size: 34px; color: #004281; }

.screen__title-color { display: block; margin-top: -5px; font-family: "Circe-Bold", Arial, sans-serif; font-size: 46px; letter-spacing: -0.04em; }

.screen__list { margin-top: 20px; margin-bottom: 40px; font-size: 21px; line-height: 1.52; }

.screen__list-fa { margin-right: 12px; color: #b22257; }

.screen__list-fa.fa { font-size: 13px; }

.screen__row:after { content: ""; display: table; clear: both; }

.screen__btn + .screen__btn { margin-left: 20px; }

/* Eco ----------------------------------- */
.eco { padding-top: 75px; padding-bottom: 90px; }

.eco__title { font-family: "Circe-Bold", Arial, sans-serif; font-size: 34px; color: #004281; text-align: center; }

.eco__title-color { display: block; margin-top: -7px; font-family: "Circe-Light", Arial, sans-serif; }

.eco__list { max-width: 900px; margin-top: 5px; margin-left: auto; margin-right: auto; font-size: 0; }

.eco__list-item { display: inline-block; vertical-align: top; margin-top: 30px; width: 33.33%; text-align: center; font-size: 19px; line-height: 1.25; }

.eco__list-item:nth-child(3n - 1) { position: relative; left: 5px; }

.eco__list-item:nth-child(3n) { position: relative; left: -15px; }

.eco__list-icon { height: 75px; font-size: 0; text-align: center; }

.eco__list-icon:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }

.eco__list-text { margin-left: auto; margin-right: auto; }

.eco__list-item:nth-child(1) .eco__list-text { max-width: 275px; }

.eco__list-item:nth-child(2) .eco__list-text { max-width: 195px; }

.eco__list-item:nth-child(3) .eco__list-text { max-width: 170px; }

.eco__list-item:nth-child(4) .eco__list-text { max-width: 210px; }

.eco__list-item:nth-child(5) .eco__list-text { max-width: 150px; }

.eco__list-item:nth-child(6) .eco__list-text { max-width: 145px; }

/* Feed ----------------------------------- */
.feed { padding-top: 50px; padding-bottom: 55px; background-color: #0068cc; }

.feed__title { font-family: "Circe-Regular", Arial, sans-serif; font-size: 29px; line-height: 1.41; text-transform: uppercase; text-align: center; color: #ffffff; }

.feed__title-color { display: block; font-family: "Circe-Bold", Arial, sans-serif; }

.feed__btn { position: relative; margin-top: 25px; }

.feed__btn:before, .feed__btn:after { content: ''; position: absolute; top: 15px; }

.feed__btn:before { background-image: url("../img/icons.png"); width: 81px; height: 23px; background-position: -106px -27px; right: 100%; margin-right: 30px; }

.feed__btn:after { background-image: url("../img/icons.png"); width: 81px; height: 23px; background-position: -106px 0px; left: 100%; margin-left: 30px; }

/* Why ----------------------------------- */
.why { padding-top: 55px; padding-bottom: 95px; background: url(../img/bg/why.jpg) center center no-repeat; background-size: cover; }

.why__list { margin-left: auto; margin-right: auto; max-width: 980px; font-size: 0; }

.why__list-item { margin-top: 40px; display: inline-block; vertical-align: top; padding: 0 20px; width: 33.33%; font-size: 19px; text-align: center; }

.why__list-icon { color: #b22257; }

.why__list-icon.fa { font-size: 38px; }

.why__list-title { margin-top: 15px; font-family: "Circe-Bold", Arial, sans-serif; font-size: 24px; }

.why__list-text { margin-top: 10px; line-height: 1.25; }

.why__btn { margin-top: 75px; }

/* =================================== Other Styles @mixin ================================== */
